let questNumb = 0
let result
let answer
let log = {}
if (localStorage.getItem('test') == null) {
    localStorage.setItem('test', JSON.stringify(log))
} 
if (localStorage.getItem('test') != null) {
    log = JSON.parse(localStorage.getItem('test'))
    questNumb = Object.keys(log).length - 1 >= 0 ? Object.keys(log).length : 0
    
}

const questions = [
    'Уверенный ли ты пользователь ПК?',
    'Наслышан ли ты о проблемах, связанных с сетевой безопастностью?',
    'Ответственно ли ты относишься к сохранности своей информации?',
    'Пользуешься ли ты двухфакторной аутентификацией?',
    'Ты пользуешься одним паролем на разных сайтах?',
    'Используешь ли ты антивирус на своем компьютере?'
]

const test_results = {
    0: "Ты отлично разибраешься в информационной безопасности, ты мололдец!",
    1: "Ты неплохо разбираешься в информационной безопасности, однако тебе есть куда расти.",
    2: "Пока что твои познания не велики, но тебе есть куда расти.",
    3: "Ты еще в начале пути познаний о информационной безопасности."
}

function setLog() {
    localStorage.setItem('test', JSON.stringify(log))
}

function clearResults() {
    questNumb = 0
    result = 0
    answer = null
    log = {}
    localStorage.setItem('test', JSON.stringify(log))
    render(questNumb)

}

function render(n) {
    if (questions.length == Object.keys(log).length) {
        getResult()
        return
    }

    $('.test__content')[0].reset()
    $('.test__question').text(questions[n])
    $('.test__input').attr("checked", false)
    $('.button[data-btn-type="result"]').addClass('hide')
    $('.button[data-btn-type="next"]').removeClass('hide')
    $('.test__options').removeClass('hide')
    $('.test__navigation').removeClass('hide')
    $('.test__question').removeClass('hide')
    $('.result').addClass('hide')
    setLog()
    answer = false

    if (log[n] != undefined) {
        $('.test__input[data-answ="'+parseInt(log[n])+'"]').attr("checked", true)
        answer = parseInt(log[n])
        $('.button[data-btn-type="next"]').removeClass('button_unactive')
        $('.button[data-btn-type="result"]').removeClass('button_unactive')
    } else {
        $('.button[data-btn-type="next"]').addClass('button_unactive')
        $('.button[data-btn-type="result"]').addClass('button_unactive')
    }

    if (n == 0) {
        $('.button[data-btn-type="prev"]').addClass('button_unactive')
    } else {
        $('.button[data-btn-type="prev"]').removeClass('button_unactive')
    }

    if (n == questions.length - 1) {
        $('.button[data-btn-type="result"]').removeClass('hide')
        $('.button[data-btn-type="next"]').addClass('hide')
    }
}

function getResult() {
    result = 0
    for (key in log) {
        result += log[key]
    }
    $('.result').removeClass('hide')
    $('.test__options').addClass('hide')
    $('.test__navigation').addClass('hide')
    $('.test__question').addClass('hide')

    if (result > questions.length && result <= questions.length * 2) {
        $('.result__text').text(test_results[0])
    }
    if (result > 0 && result <= questions.length) {
        $('.result__text').text(test_results[1])
    }
    if (result > -(questions.length) && result <= 0) {
        $('.result__text').text(test_results[2])
    }
    if (result >= -(questions.length * 2) && result <= -(questions.length)) {
        $('.result__text').text(test_results[3])
    }
    console.log(result);
}

render(questNumb)

$('.test__option').click(function(e) {
    answer = parseInt($(this).children('.test__input').attr('data-answ'))
    $('.button[data-btn-type="next"]').removeClass('button_unactive')
    $('.button[data-btn-type="result"]').removeClass('button_unactive')
})

$('.button[data-btn-type="next"]').click(e => {
    if (answer && questNumb < questions.length - 1) {
        log[questNumb] = answer
        render(++questNumb)
    }
})

$('.button[data-btn-type="result"]').click(e => {
    if (answer && questNumb == questions.length - 1) {
        log[questNumb] = answer
        setLog()
        getResult()
    }
})

$('.button[data-btn-type="prev"]').click(e => {
    if (questNumb > 0) {
        render(--questNumb)
    }
})

$('.button[data-btn-type="reset"]').click(clearResults)